.search-form {
  text-align: center;
  background-color: #2f496b;
  width: 100%;
  padding: 20px 0;
  position: fixed;
  top: 0;
  left: 0;
}

.search-form input {
  text-align: center;
  vertical-align: middle;
  border: none;
  outline: none;
  width: 300px;
  height: 48px;
  padding: 0 20px;
  font-size: 18px;
}

.search-form input::placeholder {
  text-align: center;
  color: #000;
  font-size: 18px;
}

.search-form input:focus {
  box-shadow: inset 0 0 3px #000;
}

.search-form button {
  vertical-align: middle;
  cursor: pointer;
  border: none;
  width: 100px;
  height: 48px;
  font-size: 18px;
  transition: all .3s ease-in-out;
}

.search-form button:hover {
  color: #fff;
  background-color: #8fbc99;
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  max-width: 1340px;
  margin: 120px auto 0;
  display: flex;
}

.photo-card {
  outline: 2px solid #2f496b;
  width: 320px;
  height: 100%;
  transition: transform .3s ease-in-out;
}

.photo-card a {
  text-decoration: none;
}

.photo-card img {
  object-fit: cover;
  width: 100%;
  height: 200px;
}

.photo-card:hover {
  transform: scale(1.02);
}

.info {
  text-align: center;
  margin: auto 0;
  padding: 15px;
  display: flex;
}

